/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
  display: inline-block;
}

h2::after {
  content: "";
  background-color: #84d9bc;
  width: 102%;
  height: 5px;
  display: block;
  margin-top: -1.4rem;
}

.modificadorLinea::after {
  background-color: #FFFFFF;
  content: "";
  width: 102%;
  height: 5px;
  display: block;
  margin-top: -1.4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.espacio {
  margin: 7rem 0;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.imagen {
  animation-name: saltar;
  animation-duration: 2s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}

@keyframes saltar {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}
.header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url(../img/header.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 60%;
}
.header__contenido {
  padding: 5rem 0;
}
.header__logo {
  width: 20rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__logo {
    margin: 0;
  }
}

.eslogan {
  padding: 10rem 0;
}
.eslogan__texto {
  color: #FFFFFF;
  margin: 0;
  margin-bottom: 3rem;
  font-size: 4.5rem;
}
@media (min-width: 768px) {
  .eslogan__texto {
    font-size: 6rem;
    max-width: 50%;
  }
}
@media (min-width: 1024px) {
  .eslogan__texto {
    max-width: 40%;
  }
}
.eslogan__btn {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.eslogan__btn:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .eslogan__btn {
    width: auto;
    display: inline-block;
  }
}

@media (min-width: 768px) {
  .creacion__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.creacion__contenido {
  margin-top: 5rem;
}
.creacion__header {
  color: #313c4e;
}

.alcance {
  background-color: #84d9bc;
  padding: 3rem 0;
}
.alcance__heading {
  color: #313c4e;
}
.alcance__contenido {
  text-align: left;
}
@media (min-width: 768px) {
  .alcance__grid {
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: center;
    gap: 3rem;
  }
}
.alcance__heading {
  margin: 5rem 0 0 0;
}
@media (min-width: 768px) {
  .alcance__heading {
    font-size: 3.6rem;
  }
}
.alcance__img {
  transition-property: transform;
  transition-duration: 300ms;
}
.alcance__img:hover {
  transform: scale(1.1);
}

.estadisticas__heading {
  color: #313c4e;
}

.planes {
  padding: 5rem 0;
  background-color: #313c4e;
}
@media (min-width: 768px) {
  .planes {
    padding-bottom: 10rem;
  }
}
.planes__planes {
  display: grid;
  gap: 3rem;
  grid-template-rows: repeat(3, 1fr);
  justify-content: center;
}
@media (min-width: 768px) {
  .planes__planes {
    grid-template-rows: none;
    grid-template-columns: repeat(3, 1fr);
    max-width: 74rem;
    margin-inline: auto;
    gap: 0rem;
  }
}
.planes__negocio {
  grid-row: 1/2;
}
@media (min-width: 768px) {
  .planes__negocio {
    grid-column: 2/3;
  }
}
.planes__card {
  background-color: #FFFFFF;
  border-radius: 10px;
  padding: 1rem;
  margin: 1rem;
  padding: 15rem 5rem 5rem 5rem;
  text-align: center;
  transition-property: transform;
  transition-duration: 300ms;
}
.planes__card:hover {
  transform: scale(1.1);
}
@media (min-width: 768px) {
  .planes__card {
    padding: 15rem 0 5rem;
    margin: 0;
  }
}
.planes__inicio, .planes__negocio, .planes__pro {
  background-image: url(../img/icono_inicio.svg);
  background-repeat: no-repeat;
  background-size: 10rem;
  background-position: 50% 2rem;
}
.planes__negocio {
  background-image: url(../img/icono_negocio.svg);
}
.planes__pro {
  background-image: url(../img/icono_pro.svg);
}
.planes__card--primario {
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .planes__card--primario {
    margin: -3rem 0;
  }
}
.planes__titulo {
  color: #FFFFFF;
}
.planes__texto {
  color: #FFFFFF;
}
.planes__info {
  margin-bottom: 10rem;
}
@media (min-width: 1024px) {
  .planes__info {
    margin-bottom: 5rem;
  }
}
.planes__precio {
  font-weight: 700;
}

li {
  margin-bottom: 1rem;
}

.btn {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  margin-top: 2rem;
  border: 2px solid #FFFFFF;
}
.btn:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
    display: inline-block;
  }
}
@media (min-width: 768px) {
  .btn {
    margin-top: 0;
  }
}

.testimoniales__app {
  margin-inline: auto;
}
@media (min-width: 768px) {
  .testimoniales__app {
    margin: 0;
  }
}
@media (min-width: 768px) {
  .testimoniales__grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 5rem;
    align-items: center;
    margin-inline: auto;
  }
}
@media (min-width: 1024px) {
  .testimoniales__grid {
    grid-template-columns: 1fr 2fr;
  }
}
.testimoniales__card {
  background-color: #84d9bc;
  padding: 2rem 2rem 2rem 7rem;
  margin: 2rem 0;
  border: solid 5px #313c4e;
  border-radius: 1rem;
  position: relative;
}
.testimoniales__card:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .testimoniales__card {
    padding: 2rem 2rem 1rem 7rem;
  }
}
.testimoniales__card::after {
  position: absolute;
  content: "";
  background-image: url(../img/comilla.svg);
  display: block;
  width: 4rem;
  height: 4rem;
  background-repeat: no-repeat;
  top: 3rem;
  left: 1rem;
}
@media (min-width: 1024px) {
  .testimoniales__card {
    padding: 2rem 2rem 2rem 7rem;
    max-width: 65%;
  }
  .testimoniales__card:nth-child(2) {
    margin-left: 10%;
    margin-bottom: 5rem;
  }
  .testimoniales__card:nth-child(3) {
    margin-left: 20%;
    margin-bottom: 5rem;
  }
  .testimoniales__card:nth-child(4) {
    margin-left: 30%;
  }
}
@media (min-width: 768px) {
  .testimoniales__texto {
    font-size: 1.5rem;
    margin: 0;
  }
}
@media (min-width: 1024px) {
  .testimoniales__texto {
    font-size: 2rem;
  }
}

.footer {
  background-color: #313c4e;
  margin-top: 5rem;
}
.footer__img {
  width: 20rem;
  margin-inline: auto;
}
@media (min-width: 768px) {
  .footer__img {
    margin: 0;
  }
}
.footer__texto {
  color: #FFFFFF;
}
.footer__heading {
  text-align: left;
  color: #84d9bc;
}
.footer__copy {
  border-top: 2px solid #84d9bc;
}
.footer__copy p {
  margin: 2rem 0 0 0;
  color: #FFFFFF;
  font-size: 1rem;
  text-align: center;
}
.footer__grid {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 1fr;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.footer__enlaces {
  margin-bottom: 3rem;
}
.footer__enlaces:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .footer__enlaces {
    margin-bottom: 0;
  }
}
.footer__enlaces:nth-child(2) {
  margin-top: 5rem;
}
.footer ul {
  margin: 0;
}